import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal, Form, Col, InputGroup, Row } from 'react-bootstrap';
import { connect } from 'react-redux';

import { receiveCustomerList, createOrderAction, resetOrderState } from '../../actions';

import Loader from '../Loader/Loader';

class SimpleOrderCreate extends React.Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.customerRef = React.createRef();
    this.amountRef = React.createRef();
    this.paymentRef = React.createRef();
    this.billRef = React.createRef();
    this.state = {
      validated: false
    };
  }
  componentDidMount() {
    this.props.receiveCustomerList();
  }
  componentWillUnmount() {
    this.props.resetState();
  }
  componentDidUpdate(prevProps) {
    const { order = {} } = this.props;
    const prevOrder = prevProps.order || {};
    if (order.loading !== prevOrder.loading) {
      if (!order.loading && order.success) {
        this.handleSuccess();
      } else if (order.error) {
        console.error(order.error);
      }
    }
  }

  handleClose = () => {
    this.props.onHandleClose();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSuccess = () => {
    this.props.onHandleSuccess();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSubmit = (event) => {
    const form = this.formRef.current;
    event.preventDefault();
    if (form.checkValidity()) {
      const customerUuid = this.customerRef.current.value;
      const amount = this.amountRef.current.value;
      const paymentDone = this.paymentRef.current.checked;
      const remark = this.billRef.current.value || '';
      this.props.createOrder({
        customerUuid,
        paymentDone,
        remark,
        items: {
          LUMP_SUM: {
            quantity: 1,
            price: Number(amount)
          }
        }
      });
      this.setState({
        validated: false
      });
    } else {
      event.stopPropagation();
      this.setState({
        validated: true
      });
    }
  };

  getForm = (validated) => {
    const { customers = {}, order = {} } = this.props;
    const { loading = true } = customers;
    const { loading: orderLoading = false } = order;
    if (loading || orderLoading) {
      return <Loader />;
    }
    const customerRecords =
      (customers.customers && customers.customers.sort((a, b) => a.name.localeCompare(b.name))) ||
      [];
    const customerRow = [];
    customerRecords.forEach((customer) => {
      customerRow.push(
        <option key={`OPT:${customer.customerUuid}`} value={customer.customerUuid}>
          {customer.name}
        </option>
      );
    });
    return (
      <Form
        noValidate
        validated={validated}
        ref={this.formRef}
        onSubmit={(e) => this.handleSubmit(e)}>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalType">
          <Form.Label column sm={2}>
            Customer
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Select ref={this.customerRef}>{customerRow}</Form.Select>
              <Form.Control.Feedback type="invalid">Select a customer</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Check ref={this.paymentRef} type="switch" id="paid-switch" label="Payment Done" />
          <Form.Label column sm={2}>
            Total
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={255}
                autoComplete="off"
                type="number"
                placeholder="Total amount"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.amountRef}
              />
              <Form.Control.Feedback type="invalid">Enter total amount</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Label column sm={2}>
            Bill No.
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={5}
                autoComplete="off"
                type="text"
                placeholder="Bill Number"
                aria-describedby="inputGroupPrepend"
                ref={this.billRef}
              />
              <Form.Control.Feedback type="invalid">Enter bill number</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
      </Form>
    );
  };

  render() {
    const { show } = this.props;
    const { validated } = this.state;
    const formComponent = this.getForm(validated);
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Complete Order</Modal.Title>
        </Modal.Header>
        <Modal.Body>{formComponent}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={this.handleSubmit}>
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
SimpleOrderCreate.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func
};
const mapStateToProps = (state) => ({
  customers: state.customers,
  order: state.order
});
const mapDispatchToProps = (dispatch) => ({
  receiveCustomerList: () => dispatch(receiveCustomerList()),
  createOrder: (payload) => dispatch(createOrderAction(payload)),
  resetState: () => dispatch(resetOrderState())
});
export default connect(mapStateToProps, mapDispatchToProps)(SimpleOrderCreate);
