import AxiosService from './AxiosService';

const API_HOST = 'https://balaji-balaji496001-gmailcom.vercel.app';
export async function fetchCustomersList() {
  try {
    const url = `${API_HOST}/api/customer`;
    const { data } = await AxiosService.getRequest({
      url
    });
    return data;
  } catch (error) {
    return Promise.reject(error);
  }
}
export async function addCustomer(data) {
  try {
    const url = `${API_HOST}/api/customer`;
    const { response } = await AxiosService.postRequest({
      url,
      data
    });
    return response;
  } catch (error) {
    return Promise.reject(error);
  }
}
export async function editCustomer(id, data) {
  try {
    const url = `${API_HOST}/api/customer/${id}`;
    const { response } = await AxiosService.putRequest({
      url,
      data
    });
    return response;
  } catch (error) {
    return Promise.reject(error);
  }
}
export async function removeCustomer(id) {
  try {
    const url = `${API_HOST}/api/customer/${id}`;
    const { data } = await AxiosService.deleteRequest({
      url
    });
    return data;
  } catch (error) {
    return Promise.reject(error);
  }
}
