import { Navbar, Nav, Container } from 'react-bootstrap';
import { useState } from 'react';
import { Link } from 'react-router-dom';

const NavBar = () => {
  const [expanded, setExpanded] = useState(false);
  return (
    <Navbar collapseOnSelect fixed="top" expand="sm" bg="dark" variant="dark" expanded={expanded}>
      <Container>
        <Navbar.Toggle
          aria-controls="responsive-navbar-nav"
          onClick={() => setExpanded(expanded ? false : 'expanded')}
        />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav>
            <Nav.Link as={Link} to="/home" onClick={() => setExpanded(false)}>
              Dashboard
            </Nav.Link>
            <Nav.Link as={Link} to="/order" onClick={() => setExpanded(false)}>
              Sale
            </Nav.Link>
            <Nav.Link as={Link} to="/payment" onClick={() => setExpanded(false)}>
              Payment
            </Nav.Link>
            <Nav.Link as={Link} to="/item" onClick={() => setExpanded(false)}>
              Item
            </Nav.Link>
            <Nav.Link as={Link} to="/customer" onClick={() => setExpanded(false)}>
              Customer
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
        <Navbar.Brand>Balaji Namkeen</Navbar.Brand>
      </Container>
    </Navbar>
  );
};
export default NavBar;
