import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal, Form, Col, InputGroup, Row } from 'react-bootstrap';
import { createCustomerAction, resetCustomerState } from '../../actions';
import { connect } from 'react-redux';
import Loader from '../Loader/Loader';

class CustomerCreate extends React.Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.nameRef = React.createRef();
    this.phoneRef = React.createRef();
    this.addRef = React.createRef();
    this.state = {
      validated: false
    };
  }
  componentDidUpdate(prevProps) {
    const { customer = {} } = this.props;
    const prevCustomer = prevProps.customer || {};
    if (customer.loading !== prevCustomer.loading) {
      if (!customer.loading && customer.success) {
        this.handleClose();
      } else if (customer.error) {
        console.error(customer.error);
      }
    }
  }
  componentWillUnmount() {
    this.props.resetState();
  }

  handleClose = () => {
    this.props.onHandleClose();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSuccess = () => {
    this.props.onHandleClose();
    this.formRef.current.reset();
    this.setState({ validated: false });
    this.props.onHandleSuccess();
  };

  handleSubmit = (event) => {
    const form = this.formRef.current;
    event.preventDefault();
    if (form.checkValidity()) {
      const name = this.nameRef.current.value;
      const phone = this.phoneRef.current.value;
      const address = this.addRef.current.value;
      this.props.createCustomer({ name, phone, address });
      this.setState({
        validated: false
      });
    } else {
      event.stopPropagation();
      this.setState({
        validated: true
      });
    }
  };

  getForm = (validated) => {
    const { customer = {} } = this.props;
    const { loading = false } = customer;
    if (loading) {
      return <Loader />;
    }
    return (
      <Form
        noValidate
        validated={validated}
        ref={this.formRef}
        onSubmit={(e) => this.handleSubmit(e)}>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalType">
          <Form.Label column sm={2}>
            Name
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={255}
                autoComplete="off"
                type="text"
                placeholder="Name"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.nameRef}
              />
              <Form.Control.Feedback type="invalid">Enter a Name.</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Label column sm={2}>
            Phone
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength="10"
                autoComplete="off"
                type="number"
                placeholder="Phone Number"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.phoneRef}
                min="0"
              />
              <Form.Control.Feedback type="invalid">Enter a Phone Number</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalLink">
          <Form.Label column sm={2}>
            Address
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={255}
                type="text"
                autoComplete="off"
                placeholder="Address"
                aria-describedby="inputGroupPrepend"
                ref={this.addRef}
              />
            </InputGroup>
          </Col>
        </Form.Group>
      </Form>
    );
  };

  render() {
    const { show } = this.props;
    const { validated } = this.state;
    const formComponent = this.getForm(validated);
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Register Customer</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ textAlign: 'center' }}>{formComponent}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={this.handleSubmit}>
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
CustomerCreate.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func
};
const mapStateToProps = (state) => ({
  customer: state.customer
});

const mapDispatchToProps = (dispatch) => ({
  createCustomer: (payload) => dispatch(createCustomerAction(payload)),
  resetState: () => dispatch(resetCustomerState())
});
export default connect(mapStateToProps, mapDispatchToProps)(CustomerCreate);
