import {
  DELETE_PAYMENT,
  DELETE_PAYMENT_SUCCESS,
  DELETE_PAYMENT_FAILURE,
  DEFAULT_PAYMENT_STATE,
  CREATE_PAYMENT,
  CREATE_PAYMENT_SUCCESS,
  CREATE_PAYMENT_FAILURE,
  EDIT_PAYMENT,
  EDIT_PAYMENT_FAILURE,
  EDIT_PAYMENT_SUCCESS
} from '../actions';

const defaultState = {};
function payment(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case DELETE_PAYMENT: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case DELETE_PAYMENT_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case DELETE_PAYMENT_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case CREATE_PAYMENT: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case CREATE_PAYMENT_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case CREATE_PAYMENT_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case EDIT_PAYMENT: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case EDIT_PAYMENT_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case EDIT_PAYMENT_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_PAYMENT_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default payment;
