import { call, put, takeLatest } from 'redux-saga/effects';

import { fetchPaymentsList } from '../utils/fetchUtil';
import {
  FETCH_PAYMENT_LIST,
  receivePaymentListSuccess,
  receivePaymentListFailure
} from '../actions';

function* getSaga() {
  try {
    const response = yield call(fetchPaymentsList);
    yield put(receivePaymentListSuccess(response));
  } catch (e) {
    yield put(receivePaymentListFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(FETCH_PAYMENT_LIST, getSaga);
}
