export const FETCH_ORDER_LIST = 'FETCH_ORDER_LIST';
export const FETCH_ORDER_LIST_SUCCESS = 'FETCH_ORDER_LIST_SUCCESS';
export const FETCH_ORDER_LIST_FAILURE = 'FETCH_ORDER_LIST_FAILURE';
export const DEFAULT_ORDERS_STATE = 'DEFAULT_ORDERS_STATE';
export function receiveOrderList() {
  return {
    type: FETCH_ORDER_LIST
  };
}

export function receiveOrderListSuccess(payload = {}) {
  return {
    type: FETCH_ORDER_LIST_SUCCESS,
    payload
  };
}

export function receiveOrderListFailure(error) {
  return {
    type: FETCH_ORDER_LIST_FAILURE,
    error
  };
}

export function resetOrdersState() {
  return {
    type: DEFAULT_ORDERS_STATE
  };
}
