import {
  FETCH_ORDER_LIST,
  FETCH_ORDER_LIST_SUCCESS,
  FETCH_ORDER_LIST_FAILURE,
  DEFAULT_ORDERS_STATE
} from '../actions';

const defaultState = {};
function orders(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case FETCH_ORDER_LIST: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case FETCH_ORDER_LIST_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case FETCH_ORDER_LIST_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_ORDERS_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default orders;
