import React from 'react';
import { Button, Card, Container } from 'react-bootstrap';

import PaymentTable from './PaymentTable';
import PaymentCreate from './PaymentCreate';

import { receivePaymentList } from '../../actions';
import { connect } from 'react-redux';

class Payment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showCreate: false
    };
  }
  onCreateShow = () => {
    this.setState({
      showCreate: true
    });
  };
  onCreateClose = () => {
    this.setState({
      showCreate: false
    });
  };
  onCreateSuccess = () => {
    this.props.receivePaymentList();
    this.setState({
      showCreate: false
    });
  };
  render() {
    const { showCreate } = this.state;
    return (
      <Container fluid md="auto" className="customer-main">
        <PaymentCreate
          show={showCreate}
          onHandleClose={this.onCreateClose}
          onHandleSuccess={this.onCreateSuccess}
        />
        <Card style={{ backgroundColor: '#FFFFE0' }}>
          <Card.Header>
            Payment Details
            <Button size="sm" variant="primary" className="float-end" onClick={this.onCreateShow}>
              Add
            </Button>
          </Card.Header>
          <Card.Body>
            <PaymentTable />
          </Card.Body>
        </Card>
      </Container>
    );
  }
}
const mapDispatchToProps = (dispatch) => ({
  receivePaymentList: () => dispatch(receivePaymentList())
});
export default connect(null, mapDispatchToProps)(Payment);
