import React from 'react';
import { Button, Card, Container } from 'react-bootstrap';
import { connect } from 'react-redux';

//import OrderCreate from './OrderCreate';
import OrderTable from './OrderTable';
import SimpleOrderCreate from './SimpleOrderCreate';
import './Order.css';

import { receiveOrderList } from '../../actions';

class Order extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showCreate: false
    };
  }
  onCreateShow = () => {
    this.setState({
      showCreate: true
    });
  };
  onCreateClose = () => {
    this.setState({
      showCreate: false
    });
  };
  onCreateSuccess = () => {
    this.props.recieveOrderList();
    this.setState({
      showCreate: false
    });
  };
  render() {
    const { showCreate } = this.state;
    return (
      <Container fluid md="auto" className="order-main">
        <SimpleOrderCreate
          show={showCreate}
          onHandleClose={this.onCreateClose}
          onHandleSuccess={this.onCreateSuccess}
        />
        <Card style={{ backgroundColor: '#FFFFE0' }}>
          <Card.Header>
            Order List
            <Button size="sm" variant="primary" className="float-end" onClick={this.onCreateShow}>
              Add
            </Button>
          </Card.Header>
          <Card.Body>
            <OrderTable />
          </Card.Body>
        </Card>
      </Container>
    );
  }
}
const mapDispatchToProps = (dispatch) => ({
  recieveOrderList: () => dispatch(receiveOrderList())
});
export default connect(null, mapDispatchToProps)(Order);
