import React from 'react';
import './Customer.css';
import { Card, Container, Button } from 'react-bootstrap';
import CustomerTable from './CustomerTable';
import CustomerCreate from './CustomerCreate';
import { receiveCustomerList } from '../../actions';
import { connect } from 'react-redux';

class Customer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showCreate: false
    };
  }
  onCreateShow = () => {
    this.setState({
      showCreate: true
    });
  };
  onCreateClose = () => {
    this.setState({
      showCreate: false
    });
  };
  onCreateSuccess = () => {
    this.props.receiveCustomerList();
    this.setState({
      showCreate: false
    });
  };
  render() {
    const { showCreate } = this.state;
    return (
      <Container fluid md="auto" className="customer-main">
        <CustomerCreate
          show={showCreate}
          onHandleClose={this.onCreateClose}
          onHandleSuccess={this.onCreateSuccess}
        />
        <Card style={{ backgroundColor: '#FFFFE0' }}>
          <Card.Header>
            Customer Details
            <Button size="sm" variant="primary" className="float-end" onClick={this.onCreateShow}>
              Add
            </Button>
          </Card.Header>
          <Card.Body>
            <CustomerTable />
          </Card.Body>
        </Card>
      </Container>
    );
  }
}
const mapDispatchToProps = (dispatch) => ({
  receiveCustomerList: () => dispatch(receiveCustomerList())
});
export default connect(null, mapDispatchToProps)(Customer);
