import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal, Form, Col, InputGroup, Row } from 'react-bootstrap';
import { connect } from 'react-redux';

import { receiveCustomerList, createPaymentAction, resetPaymentState } from '../../actions';

import Loader from '../Loader/Loader';

class PaymentCreate extends React.Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.customerRef = React.createRef();
    this.amountRef = React.createRef();
    this.state = {
      validated: false
    };
  }
  componentDidMount() {
    this.props.receiveCustomerList();
  }
  componentWillUnmount() {
    this.props.resetState();
  }
  componentDidUpdate(prevProps) {
    const { payment = {} } = this.props;
    const prevOrder = prevProps.payment || {};
    if (payment.loading !== prevOrder.loading) {
      if (!payment.loading && payment.success) {
        this.handleSuccess();
      } else if (payment.error) {
        console.error(payment.error);
      }
    }
  }
  handleClose = () => {
    this.props.onHandleClose();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSuccess = () => {
    this.props.onHandleSuccess();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSubmit = (event) => {
    const form = this.formRef.current;
    event.preventDefault();
    if (form.checkValidity()) {
      const customerUuid = this.customerRef.current.value;
      const amount = this.amountRef.current.value;
      this.props.createPayment({
        customerUuid,
        amount: Number(amount)
      });
      this.setState({
        validated: false
      });
    } else {
      event.stopPropagation();
      this.setState({
        validated: true
      });
    }
  };

  getForm = (validated) => {
    const { customers = {}, payment = {} } = this.props;
    const { loading = true } = customers;
    const { loading: orderLoading = false } = payment;
    if (loading || orderLoading) {
      return <Loader />;
    }
    const customerRecords =
      (customers.customers && customers.customers.sort((a, b) => a.name.localeCompare(b.name))) ||
      [];
    const customerRow = [];
    customerRecords.forEach((customer) => {
      customerRow.push(
        <option key={`OPT:${customer.customerUuid}`} value={customer.customerUuid}>
          {customer.name}
        </option>
      );
    });
    return (
      <Form
        noValidate
        validated={validated}
        ref={this.formRef}
        onSubmit={(e) => this.handleSubmit(e)}>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalType">
          <Form.Label column sm={2}>
            Customer
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Select ref={this.customerRef}>{customerRow}</Form.Select>
              <Form.Control.Feedback type="invalid">Select a customer</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Label column sm={2}>
            Total
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={255}
                autoComplete="off"
                type="text"
                placeholder="Total amount"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.amountRef}
              />
              <Form.Control.Feedback type="invalid">Enter total amount</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
      </Form>
    );
  };

  render() {
    const { show } = this.props;
    const { validated } = this.state;
    const formComponent = this.getForm(validated);
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Complete Payment</Modal.Title>
        </Modal.Header>
        <Modal.Body>{formComponent}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={this.handleSubmit}>
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
PaymentCreate.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func
};
const mapStateToProps = (state) => ({
  customers: state.customers,
  payment: state.payment
});
const mapDispatchToProps = (dispatch) => ({
  receiveCustomerList: () => dispatch(receiveCustomerList()),
  createPayment: (payload) => dispatch(createPaymentAction(payload)),
  resetState: () => dispatch(resetPaymentState())
});
export default connect(mapStateToProps, mapDispatchToProps)(PaymentCreate);
