export const DELETE_ITEM = 'DELETE_ITEM';
export const DELETE_ITEM_SUCCESS = 'DELETE_ITEM_SUCCESS';
export const DELETE_ITEM_FAILURE = 'DELETE_ITEM_FAILURE';
export const CREATE_ITEM = 'CREATE_ITEM';
export const CREATE_ITEM_SUCCESS = 'CREATE_ITEM_SUCCESS';
export const CREATE_ITEM_FAILURE = 'CREATE_ITEM_FAILURE';
export const EDIT_ITEM = 'EDIT_ITEM';
export const EDIT_ITEM_SUCCESS = 'EDIT_ITEM_SUCCESS';
export const EDIT_ITEM_FAILURE = 'EDIT_ITEM_FAILURE';
export const DEFAULT_ITEM_STATE = 'DEFAULT_ITEM_STATE';
export function removeItemAction(payload = {}) {
  return {
    type: DELETE_ITEM,
    payload
  };
}

export function removeItemSuccess(payload = {}) {
  return {
    type: DELETE_ITEM_SUCCESS,
    payload
  };
}

export function removeItemFailure(error) {
  return {
    type: DELETE_ITEM_FAILURE,
    error
  };
}
export function createItemAction(payload = {}) {
  return {
    type: CREATE_ITEM,
    payload
  };
}

export function createItemSuccess(payload = {}) {
  return {
    type: CREATE_ITEM_SUCCESS,
    payload
  };
}

export function createItemFailure(error) {
  return {
    type: CREATE_ITEM_FAILURE,
    error
  };
}
export function editItemAction(id, payload = {}) {
  return {
    type: EDIT_ITEM,
    id,
    payload
  };
}

export function editItemSuccess(payload = {}) {
  return {
    type: EDIT_ITEM_SUCCESS,
    payload
  };
}

export function editItemFailure(error) {
  return {
    type: EDIT_ITEM_FAILURE,
    error
  };
}
export function resetItemState() {
  return {
    type: DEFAULT_ITEM_STATE
  };
}
