export const FETCH_ITEM_LIST = 'FETCH_ITEM_LIST';
export const FETCH_ITEM_LIST_SUCCESS = 'FETCH_ITEM_LIST_SUCCESS';
export const FETCH_ITEM_LIST_FAILURE = 'FETCH_ITEM_LIST_FAILURE';
export const DEFAULT_ITEMS_STATE = 'DEFAULT_ITEMS_STATE';
export function receiveItemList() {
  return {
    type: FETCH_ITEM_LIST
  };
}

export function receiveItemListSuccess(payload = {}) {
  return {
    type: FETCH_ITEM_LIST_SUCCESS,
    payload
  };
}

export function receiveItemListFailure(error) {
  return {
    type: FETCH_ITEM_LIST_FAILURE,
    error
  };
}

export function resetItemsState() {
  return {
    type: DEFAULT_ITEMS_STATE
  };
}
