import React from 'react';
import { Dropdown, Table } from 'react-bootstrap';
import { connect } from 'react-redux';
import { resetCustomersState, receiveCustomerList } from '../../actions';
import CustomerDelete from './CustomerDelete';
import CustomerEdit from './CustomerEdit';
import Loader from '../Loader/Loader';

class CustomerTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showDelete: false,
      showEdit: false,
      customer: {}
    };
  }
  componentDidMount() {
    this.props.receiveCustomerList();
  }
  componentWillUnmount() {
    this.props.resetState();
  }

  onDelete = (e) => {
    const targetId = e.target.id;
    const customerUuid = targetId.split(':')[1];
    const { customers = {} } = this.props;
    const customersData = customers.customers || [];
    const customer = customersData.find((record) => customerUuid === record.customerUuid);
    if (customer) {
      this.setState({
        showDelete: true,
        customer
      });
    }
  };
  onDeleteClose = () => {
    this.setState({
      showDelete: false,
      customer: {}
    });
  };
  onDeleteSuccess = () => {
    this.setState({
      showDelete: false,
      customer: {}
    });
    this.props.receiveCustomerList();
  };
  onEdit = (e) => {
    const targetId = e.target.id;
    const customerUuid = targetId.split(':')[1];
    const { customers = {} } = this.props;
    const customersData = customers.customers || [];
    const customer = customersData.find((record) => customerUuid === record.customerUuid);
    if (customer) {
      this.setState({
        showDelete: false,
        showEdit: true,
        customer
      });
    }
  };
  onEditClose = () => {
    this.setState({
      showEdit: false,
      customer: {}
    });
  };
  onEditSuccess = () => {
    this.setState({
      showEdit: false,
      customer: {}
    });
    this.props.receiveCustomerList();
  };

  render() {
    const { customers = {} } = this.props;
    const { showDelete, customer, showEdit } = this.state;
    const { loading = true } = customers;
    const customersRecord = customers.customers || [];
    if (loading) {
      return <Loader />;
    }
    const rows = [];
    customersRecord.forEach((customer) => {
      const toggleButton = (
        <Dropdown>
          <Dropdown.Toggle split variant="outline-success" id="dropdown-split-basic" />
          <Dropdown.Menu>
            <Dropdown.Item id={`EDIT:${customer.customerUuid}`} onClick={this.onEdit}>
              Edit
            </Dropdown.Item>
            <Dropdown.Item id={`DELETE:${customer.customerUuid}`} onClick={this.onDelete}>
              Delete
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      );
      const row = (
        <tr key={`ROW:${customer.customerUuid}`}>
          <td>{customer.name}</td>
          <td>{customer.phone}</td>
          <td>{customer.address}</td>
          <td>{toggleButton}</td>
        </tr>
      );
      rows.push(row);
    });
    return (
      <>
        <CustomerDelete
          show={showDelete}
          customerData={customer}
          onHandleClose={this.onDeleteClose}
          onSuccess={this.onDeleteSuccess}
        />
        <CustomerEdit
          show={showEdit}
          customerData={customer}
          onHandleClose={this.onEditClose}
          onHandleSuccess={this.onEditSuccess}
        />
        <Table responsive striped bordered hover>
          <thead>
            <tr>
              <th>Name</th>
              <th>Phone</th>
              <th>Address</th>
              <th></th>
            </tr>
          </thead>
          <tbody>{rows}</tbody>
        </Table>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  customers: state.customers
});

const mapDispatchToProps = (dispatch) => ({
  receiveCustomerList: () => dispatch(receiveCustomerList()),
  resetState: () => dispatch(resetCustomersState())
});
export default connect(mapStateToProps, mapDispatchToProps)(CustomerTable);
