import { call, put, takeLatest } from 'redux-saga/effects';

import { removePayment, addPayment, editPayment } from '../utils/fetchUtil';
import {
  DELETE_PAYMENT,
  removePaymentSuccess,
  removePaymentFailure,
  CREATE_PAYMENT,
  createPaymentSuccess,
  createPaymentFailure,
  EDIT_PAYMENT,
  editPaymentSuccess,
  editPaymentFailure
} from '../actions';

function* removeSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(removePayment, payload);
    yield put(removePaymentSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(removePaymentFailure(e));
  }
}

function* addSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(addPayment, payload);
    yield put(createPaymentSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(createPaymentFailure(e));
  }
}

function* editSaga(body) {
  const { id, payload } = body;
  try {
    const response = yield call(editPayment, id, payload);
    yield put(editPaymentSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(editPaymentFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(DELETE_PAYMENT, removeSaga);
  yield takeLatest(CREATE_PAYMENT, addSaga);
  yield takeLatest(EDIT_PAYMENT, editSaga);
}
