import {
  FETCH_ITEM_LIST,
  FETCH_ITEM_LIST_SUCCESS,
  FETCH_ITEM_LIST_FAILURE,
  DEFAULT_ITEMS_STATE
} from '../actions';

const defaultState = {};
function items(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case FETCH_ITEM_LIST: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case FETCH_ITEM_LIST_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case FETCH_ITEM_LIST_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_ITEMS_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default items;
