import React from 'react';
import { Button, Col, Dropdown, Row, Table, Container, DropdownButton } from 'react-bootstrap';
import { connect } from 'react-redux';

import { receiveCustomerList, receivePaymentList, resetPaymentsState } from '../../actions';

import Loader from '../Loader/Loader';
import PaymentEdit from './PaymentEdit';

const RECORD_OFFSET = 5;

class PaymentTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      start: 0,
      showEdit: false,
      payment: {}
    };
  }
  componentDidMount() {
    this.props.receivePaymentList();
    this.props.receiveCustomerList();
  }
  componentWillUnmount() {
    this.props.resetPaymentsState();
  }

  getCustomerName = (customerUuid) => {
    const { customers = {} } = this.props;
    const customerRecords = customers.customers;
    const customer = customerRecords.find((record) => customerUuid === record.customerUuid);
    return customer.name;
  };

  onClickNext = () => {
    const { start } = this.state;
    this.setState({
      start: start + RECORD_OFFSET
    });
  };

  onClickPrev = () => {
    const { start } = this.state;
    this.setState({
      start: start - RECORD_OFFSET
    });
  };
  onClickFilter = (e) => {
    const targetId = e.target.id;
    const customerUuid = targetId.split(':')[1];
    if (customerUuid) {
      this.setState({
        customerUuid
      });
    }
  };
  onClearFilter = () => {
    this.setState({
      customerUuid: ''
    });
  };

  onEdit = (e) => {
    const targetId = e.target.id;
    const paymentUuid = targetId.split(':')[1];
    const { payments = {} } = this.props;
    const paymentsData = payments.payments || [];
    const payment = paymentsData.find((record) => paymentUuid === record.paymentUuid);
    if (payment) {
      this.setState({
        showEdit: true,
        payment
      });
    }
  };
  onEditClose = () => {
    this.setState({
      showEdit: false,
      order: {}
    });
  };
  onEditSuccess = () => {
    this.setState({
      showEdit: false,
      order: {}
    });
    this.props.receivePaymentList();
  };

  getDropDown = () => {
    const { customers = {} } = this.props;
    const customerRecords =
      (customers.customers && customers.customers.sort((a, b) => a.name.localeCompare(b.name))) ||
      [];
    const row = [];
    row.push(
      <Dropdown.Item onClick={this.onClearFilter} id="FILTER:ALL">
        ALL
      </Dropdown.Item>
    );
    customerRecords.forEach((customer) => {
      row.push(
        <Dropdown.Item onClick={this.onClickFilter} id={`FILTER:${customer.customerUuid}`}>
          {customer.name}
        </Dropdown.Item>
      );
    });
    return (
      <DropdownButton
        variant="outline-secondary"
        title="Select Customer"
        id="input-group-dropdown-1">
        {row}
      </DropdownButton>
    );
  };

  render() {
    const { payments = {}, customers = {} } = this.props;
    const { start = 0, customerUuid = '', showEdit, payment } = this.state;
    const { loading = false } = payments;
    const { loading: customerLoading = true } = customers;
    if (loading || customerLoading) {
      return <Loader />;
    }
    // sort record based on createdAt desc
    let paymentRecord =
      (payments.payments &&
        payments.payments.sort((b, a) => Number(a.createAt) - Number(b.createAt))) ||
      [];
    // filter customer based on customerUuid
    if (customerUuid) {
      const filterPayments = paymentRecord.filter((order) => order.customerUuid === customerUuid);
      paymentRecord = filterPayments;
    }
    const rows = [];
    paymentRecord.slice(start, start + RECORD_OFFSET).forEach((payment) => {
      const { createAt = '' } = payment;
      let date = '';
      if (createAt) {
        const dateobj = new Date(Math.round(Number(createAt)));
        date = `${dateobj.getUTCDate()}-${dateobj.getUTCMonth() + 1}-${dateobj.getUTCFullYear()}`;
      }
      const toggleButton = (
        <Dropdown key={`DROP:${payment.paymentUuid}`}>
          <Dropdown.Toggle split variant="outline-success" id="dropdown-split-basic" />
          <Dropdown.Menu>
            <Dropdown.Item id={`EDIT:${payment.paymentUuid}`} onClick={this.onEdit}>
              Edit
            </Dropdown.Item>
            <Dropdown.Item id={`DELETE:${payment.paymentUuid}`}>Delete</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      );
      const row = (
        <tr>
          <td>{this.getCustomerName(payment.customerUuid)}</td>
          <td>{payment.amount}</td>
          <td>{date}</td>
          <td>{toggleButton}</td>
        </tr>
      );
      rows.push(row);
    });

    return (
      <Container fluid>
        <Row style={{ paddingBottom: '20px' }}>
          <Col>{this.getDropDown()}</Col>
          <Col xs lg={2}>
            <Button onClick={this.onClickPrev} variant="secondary" size="sm" className="float-end">
              &lt;Prev
            </Button>
          </Col>
          <Col xs lg={2}>
            <Button onClick={this.onClickNext} size="sm" variant="secondary" className="float-end">
              Next&gt;
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>
            <>
              <PaymentEdit
                show={showEdit}
                paymentData={payment}
                onHandleClose={this.onEditClose}
                onHandleSuccess={this.onEditSuccess}
              />
              <Table responsive striped bordered hover>
                <thead>
                  <tr>
                    <th>Customer</th>
                    <th>Total</th>
                    <th>Date</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>{rows}</tbody>
              </Table>
            </>
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapStateToProps = (state) => ({
  payments: state.payments,
  customers: state.customers
});

const mapDispatchToProps = (dispatch) => ({
  receivePaymentList: () => dispatch(receivePaymentList()),
  resetPaymentsState: () => dispatch(resetPaymentsState()),
  receiveCustomerList: () => dispatch(receiveCustomerList())
});
export default connect(mapStateToProps, mapDispatchToProps)(PaymentTable);
