import { call, put, takeLatest } from 'redux-saga/effects';

import { fetchItemsList } from '../utils/fetchUtil';
import { FETCH_ITEM_LIST, receiveItemListSuccess, receiveItemListFailure } from '../actions';

function* getSaga() {
  try {
    const response = yield call(fetchItemsList);
    yield put(receiveItemListSuccess(response));
  } catch (e) {
    yield put(receiveItemListFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(FETCH_ITEM_LIST, getSaga);
}
