import {
  FETCH_PAYMENT_LIST,
  FETCH_PAYMENT_LIST_SUCCESS,
  FETCH_PAYMENT_LIST_FAILURE,
  DEFAULT_PAYMENTS_STATE
} from '../actions';

const defaultState = {};
function payments(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case FETCH_PAYMENT_LIST: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case FETCH_PAYMENT_LIST_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case FETCH_PAYMENT_LIST_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_PAYMENTS_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default payments;
