import { call, put, takeLatest } from 'redux-saga/effects';

import { fetchOrdersList } from '../utils/fetchUtil';
import { FETCH_ORDER_LIST, receiveOrderListSuccess, receiveOrderListFailure } from '../actions';

function* getSaga() {
  try {
    const response = yield call(fetchOrdersList);
    yield put(receiveOrderListSuccess(response));
  } catch (e) {
    yield put(receiveOrderListFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(FETCH_ORDER_LIST, getSaga);
}
