import { call, put, takeLatest } from 'redux-saga/effects';

import { fetchCustomersList } from '../utils/fetchUtil';
import {
  FETCH_CUSTOMER_LIST,
  receiveCustomerListSuccess,
  receiveCustomerListFailure
} from '../actions';

function* getSaga() {
  try {
    const response = yield call(fetchCustomersList);
    yield put(receiveCustomerListSuccess(response));
  } catch (e) {
    yield put(receiveCustomerListFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(FETCH_CUSTOMER_LIST, getSaga);
}
