import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './store';

// External Components
import { Container, Row, Col } from 'react-bootstrap';

// Components
import './App.css';
import Home from './components/Home/Home';
import Item from './components/Item/Item';
import Order from './components/Order/Order';
import Customer from './components/Customer/Customer';
import NavBar from './components/NavBar/NavBar';
import Payment from './components/Payment/Payment';
import SideBar from './components/SideBar/SideBar';

const routes = (
  <Routes>
    <Route exact path="/" element={<Home />}></Route>
    <Route exact path="/home" element={<Home />}></Route>
    <Route exact path="/order" element={<Order />}></Route>
    <Route exact path="/customer" element={<Customer />}></Route>
    <Route exact path="/item" element={<Item />}></Route>
    <Route exact path="/payment" element={<Payment />}></Route>
  </Routes>
);
function App() {
  let isMobile = false;
  if (window.innerWidth < 720) {
    isMobile = true;
  }
  if (isMobile) {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <Container fluid className="p-0">
              <NavBar />
              <div style={{ backgroundColor: '#DCDCDC', paddingTop: '30px' }}>{routes}</div>
            </Container>
          </div>
        </Router>
      </Provider>
    );
  }
  return (
    <Provider store={store}>
      <Router>
        <div className="App">
          <Container fluid>
            <Row>
              <Col xs={3} md="auto" style={{ paddingLeft: '0' }}>
                <SideBar />
              </Col>
              <Col style={{ backgroundColor: '#DCDCDC' }}>{routes}</Col>
            </Row>
          </Container>
        </div>
      </Router>
    </Provider>
  );
}

export default App;
