import {
  DELETE_ITEM,
  DELETE_ITEM_SUCCESS,
  DELETE_ITEM_FAILURE,
  DEFAULT_ITEM_STATE,
  CREATE_ITEM,
  CREATE_ITEM_SUCCESS,
  CREATE_ITEM_FAILURE,
  EDIT_ITEM,
  EDIT_ITEM_FAILURE,
  EDIT_ITEM_SUCCESS
} from '../actions';

const defaultState = {};
function item(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case DELETE_ITEM: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case DELETE_ITEM_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case DELETE_ITEM_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case CREATE_ITEM: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case CREATE_ITEM_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case CREATE_ITEM_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case EDIT_ITEM: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case EDIT_ITEM_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case EDIT_ITEM_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_ITEM_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default item;
