import {
  DELETE_ORDER,
  DELETE_ORDER_SUCCESS,
  DELETE_ORDER_FAILURE,
  DEFAULT_ORDER_STATE,
  CREATE_ORDER,
  CREATE_ORDER_SUCCESS,
  CREATE_ORDER_FAILURE,
  EDIT_ORDER,
  EDIT_ORDER_FAILURE,
  EDIT_ORDER_SUCCESS
} from '../actions';

const defaultState = {};
function order(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case DELETE_ORDER: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case DELETE_ORDER_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case DELETE_ORDER_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case CREATE_ORDER: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case CREATE_ORDER_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case CREATE_ORDER_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case EDIT_ORDER: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case EDIT_ORDER_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case EDIT_ORDER_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_ORDER_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default order;
