export const DELETE_PAYMENT = 'DELETE_PAYMENT';
export const DELETE_PAYMENT_SUCCESS = 'DELETE_PAYMENT_SUCCESS';
export const DELETE_PAYMENT_FAILURE = 'DELETE_PAYMENT_FAILURE';
export const CREATE_PAYMENT = 'CREATE_PAYMENT';
export const CREATE_PAYMENT_SUCCESS = 'CREATE_PAYMENT_SUCCESS';
export const CREATE_PAYMENT_FAILURE = 'CREATE_PAYMENT_FAILURE';
export const EDIT_PAYMENT = 'EDIT_PAYMENT';
export const EDIT_PAYMENT_SUCCESS = 'EDIT_PAYMENT_SUCCESS';
export const EDIT_PAYMENT_FAILURE = 'EDIT_PAYMENT_FAILURE';
export const DEFAULT_PAYMENT_STATE = 'DEFAULT_PAYMENT_STATE';
export function removePaymentAction(payload = {}) {
  return {
    type: DELETE_PAYMENT,
    payload
  };
}

export function removePaymentSuccess(payload = {}) {
  return {
    type: DELETE_PAYMENT_SUCCESS,
    payload
  };
}

export function removePaymentFailure(error) {
  return {
    type: DELETE_PAYMENT_FAILURE,
    error
  };
}
export function createPaymentAction(payload = {}) {
  return {
    type: CREATE_PAYMENT,
    payload
  };
}

export function createPaymentSuccess(payload = {}) {
  return {
    type: CREATE_PAYMENT_SUCCESS,
    payload
  };
}

export function createPaymentFailure(error) {
  return {
    type: CREATE_PAYMENT_FAILURE,
    error
  };
}
export function editPaymentAction(id, payload = {}) {
  return {
    type: EDIT_PAYMENT,
    id,
    payload
  };
}

export function editPaymentSuccess(payload = {}) {
  return {
    type: EDIT_PAYMENT_SUCCESS,
    payload
  };
}

export function editPaymentFailure(error) {
  return {
    type: EDIT_PAYMENT_FAILURE,
    error
  };
}
export function resetPaymentState() {
  return {
    type: DEFAULT_PAYMENT_STATE
  };
}
