import { call, put, takeLatest } from 'redux-saga/effects';

import { removeItem, addItem, editItem } from '../utils/fetchUtil';
import {
  DELETE_ITEM,
  removeItemSuccess,
  removeItemFailure,
  CREATE_ITEM,
  createItemSuccess,
  createItemFailure,
  EDIT_ITEM,
  editItemSuccess,
  editItemFailure
} from '../actions';

function* removeSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(removeItem, payload);
    yield put(removeItemSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(removeItemFailure(e));
  }
}

function* addSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(addItem, payload);
    yield put(createItemSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(createItemFailure(e));
  }
}

function* editSaga(body) {
  const { id, payload } = body;
  try {
    const response = yield call(editItem, id, payload);
    yield put(editItemSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(editItemFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(DELETE_ITEM, removeSaga);
  yield takeLatest(CREATE_ITEM, addSaga);
  yield takeLatest(EDIT_ITEM, editSaga);
}
