import { call, put, takeLatest } from 'redux-saga/effects';

import { removeCustomer, addCustomer, editCustomer } from '../utils/fetchUtil';
import {
  DELETE_CUSTOMER,
  removeCustomerSuccess,
  removeCustomerFailure,
  CREATE_CUSTOMER,
  createCustomerSuccess,
  createCustomerFailure,
  EDIT_CUSTOMER,
  editCustomerSuccess,
  editCustomerFailure
} from '../actions';

function* removeSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(removeCustomer, payload);
    yield put(removeCustomerSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(removeCustomerFailure(e));
  }
}

function* addSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(addCustomer, payload);
    yield put(createCustomerSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(createCustomerFailure(e));
  }
}

function* editSaga(body) {
  const { payload, id } = body;
  try {
    const response = yield call(editCustomer, id, payload);
    yield put(editCustomerSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(editCustomerFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(DELETE_CUSTOMER, removeSaga);
  yield takeLatest(CREATE_CUSTOMER, addSaga);
  yield takeLatest(EDIT_CUSTOMER, editSaga);
}
