import React from 'react';
import { Dropdown, Table, Container, Button, Row, Col, DropdownButton } from 'react-bootstrap';
import { connect } from 'react-redux';

import { receiveOrderList, resetOrdersState, receiveCustomerList } from '../../actions';

import Loader from '../Loader/Loader';
import SimpleOrderEdit from './SimpleOrderEdit';
import SimpleOrderDelete from './SimpleOrderDelete';

const RECORD_OFFSET = 5;

class OrderTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      start: 0,
      showEdit: false,
      showDelete: false,
      order: {}
    };
  }
  componentDidMount() {
    this.props.receiveOrderList();
    this.props.receiveCustomerList();
  }
  componentWillUnmount() {
    this.props.resetOrdersState();
  }

  getCustomerName = (customerUuid) => {
    const { customers = {} } = this.props;
    const customerRecords = customers.customers;
    const customer = customerRecords.find((record) => customerUuid === record.customerUuid);
    return customer.name;
  };
  onClickNext = () => {
    const { start } = this.state;
    this.setState({
      start: start + RECORD_OFFSET
    });
  };

  onClickPrev = () => {
    const { start } = this.state;
    this.setState({
      start: start - RECORD_OFFSET
    });
  };
  onClickFilter = (e) => {
    const targetId = e.target.id;
    const customerUuid = targetId.split(':')[1];
    if (customerUuid) {
      this.setState({
        customerUuid
      });
    }
  };
  onClearFilter = () => {
    this.setState({
      customerUuid: ''
    });
  };
  onDelete = (e) => {
    const targetId = e.target.id;
    const orderUuid = targetId.split(':')[1];
    const { orders = {} } = this.props;
    const ordersData = orders.orders || [];
    const order = ordersData.find((record) => orderUuid === record.orderUuid);
    if (order) {
      this.setState({
        showDelete: true,
        order
      });
    }
  };
  onDeleteClose = () => {
    this.setState({
      showDelete: false,
      order: {}
    });
  };
  onDeleteSuccess = () => {
    this.setState({
      showDelete: false,
      order: {}
    });
    this.props.receiveOrderList();
  };
  onEdit = (e) => {
    const targetId = e.target.id;
    const orderUuid = targetId.split(':')[1];
    const { orders = {} } = this.props;
    const ordersData = orders.orders || [];
    const order = ordersData.find((record) => orderUuid === record.orderUuid);
    if (order) {
      this.setState({
        showEdit: true,
        order
      });
    }
  };
  onEditClose = () => {
    this.setState({
      showEdit: false,
      order: {}
    });
  };
  onEditSuccess = () => {
    this.setState({
      showEdit: false,
      order: {}
    });
    this.props.receiveOrderList();
  };
  getDropDown = () => {
    const { customers = {} } = this.props;
    const customerRecords =
      (customers.customers && customers.customers.sort((a, b) => a.name.localeCompare(b.name))) ||
      [];
    const row = [];
    row.push(
      <Dropdown.Item onClick={this.onClearFilter} id="FILTER:ALL">
        ALL
      </Dropdown.Item>
    );
    customerRecords.forEach((customer) => {
      row.push(
        <Dropdown.Item onClick={this.onClickFilter} id={`FILTER:${customer.customerUuid}`}>
          {customer.name}
        </Dropdown.Item>
      );
    });
    return (
      <DropdownButton
        variant="outline-secondary"
        title="Select Customer"
        id="input-group-dropdown-1">
        {row}
      </DropdownButton>
    );
  };
  render() {
    const { orders = {}, customers = {} } = this.props;
    const { start = 0, customerUuid = '', order, showEdit, showDelete } = this.state;
    const { loading = true } = orders;
    const { loading: customerLoading = true } = customers;
    if (loading || customerLoading) {
      return <Loader />;
    }
    // sort record based on createdAt desc
    let ordersRecord =
      (orders.orders && orders.orders.sort((b, a) => Number(a.createAt) - Number(b.createAt))) ||
      [];
    // filter customer based on customerUuid
    if (customerUuid) {
      const filterOrders = ordersRecord.filter((order) => order.customerUuid === customerUuid);
      ordersRecord = filterOrders;
    }
    const rows = [];
    ordersRecord.slice(start, start + RECORD_OFFSET).forEach((order) => {
      const { createAt = '' } = order;
      // let totalQty = 0;
      // Object.keys(items).forEach((key) => {
      //   const item = items[key];
      //   totalQty += item.quantity;
      // });
      let date = '';
      if (createAt) {
        const dateobj = new Date(Math.round(Number(createAt)));
        date = `${dateobj.getUTCDate()}-${dateobj.getUTCMonth() + 1}-${dateobj.getUTCFullYear()}`;
      }
      const toggleButton = (
        <Dropdown key={`DROP:${order.orderUuid}`}>
          <Dropdown.Toggle split variant="outline-success" id="dropdown-split-basic" />
          <Dropdown.Menu>
            <Dropdown.Item id={`EDIT:${order.orderUuid}`} onClick={this.onEdit}>
              Edit
            </Dropdown.Item>
            <Dropdown.Item id={`DELETE:${order.orderUuid}`} onClick={this.onDelete}>
              Delete
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      );
      const row = (
        <tr key={`ROW:${order.orderUuid}`}>
          <td>{this.getCustomerName(order.customerUuid)}</td>
          {/*<td>{totalQty}</td>*/}
          <td>{order.total}</td>
          <td>{date}</td>
          <td>{toggleButton}</td>
        </tr>
      );
      rows.push(row);
    });
    // const items = (
    //   <>
    //     5 balaji: 4 X 1600 <br />
    //     10balaji: 2 X 1800
    //   </>
    // );
    return (
      <Container fluid>
        <Row style={{ paddingBottom: '20px' }}>
          <Col>{this.getDropDown()}</Col>
          <Col xs lg={2}>
            <Button onClick={this.onClickPrev} variant="secondary" size="sm" className="float-end">
              &lt;Prev
            </Button>
          </Col>
          <Col xs lg={2}>
            <Button onClick={this.onClickNext} size="sm" variant="secondary" className="float-end">
              Next&gt;
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>
            <>
              <SimpleOrderEdit
                show={showEdit}
                orderData={order}
                onHandleClose={this.onEditClose}
                onHandleSuccess={this.onEditSuccess}
              />
              <SimpleOrderDelete
                show={showDelete}
                orderData={order}
                onHandleClose={this.onDeleteClose}
                onHandleSuccess={this.onDeleteSuccess}
              />
              <Table responsive striped bordered hover>
                <thead>
                  <tr>
                    <th>Customer</th>
                    {/*<th>Qty</th>*/}
                    <th>Amount</th>
                    <th>Date</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>{rows}</tbody>
              </Table>
            </>
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapStateToProps = (state) => ({
  orders: state.orders,
  customers: state.customers
});

const mapDispatchToProps = (dispatch) => ({
  receiveOrderList: () => dispatch(receiveOrderList()),
  receiveCustomerList: () => dispatch(receiveCustomerList()),
  resetOrdersState: () => dispatch(resetOrdersState())
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderTable);
