import { call, put, takeLatest } from 'redux-saga/effects';

import { removeOrder, addOrder, editOrder } from '../utils/fetchUtil';
import {
  DELETE_ORDER,
  removeOrderSuccess,
  removeOrderFailure,
  CREATE_ORDER,
  createOrderSuccess,
  createOrderFailure,
  EDIT_ORDER,
  editOrderSuccess,
  editOrderFailure
} from '../actions';

function* removeSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(removeOrder, payload);
    yield put(removeOrderSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(removeOrderFailure(e));
  }
}

function* addSaga(body) {
  const { payload } = body;
  try {
    const response = yield call(addOrder, payload);
    yield put(createOrderSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(createOrderFailure(e));
  }
}

function* editSaga(body) {
  const { id, payload } = body;
  try {
    const response = yield call(editOrder, id, payload);
    yield put(editOrderSuccess(response));
  } catch (e) {
    console.log({ e });
    yield put(editOrderFailure(e));
  }
}

export default function* saga() {
  yield takeLatest(DELETE_ORDER, removeSaga);
  yield takeLatest(CREATE_ORDER, addSaga);
  yield takeLatest(EDIT_ORDER, editSaga);
}
