import React from 'react';
import { Dropdown, Table } from 'react-bootstrap';
import { connect } from 'react-redux';

import { resetItemsState, receiveItemList } from '../../actions';

import ItemDelete from './ItemDelete';
import ItemEdit from './ItemEdit';
import Loader from '../Loader/Loader';

class ItemTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showDelete: false,
      showEdit: false,
      item: {}
    };
  }
  componentDidMount() {
    this.props.receiveItemList();
  }
  componentWillUnmount() {
    this.props.resetState();
  }
  onDelete = (e) => {
    const targetId = e.target.id;
    const itemUuid = targetId.split(':')[1];
    const { items = {} } = this.props;
    const itemsData = items.items || [];
    const item = itemsData.find((record) => itemUuid === record.itemUuid);
    if (item) {
      this.setState({
        showDelete: true,
        item
      });
    }
  };
  onDeleteClose = () => {
    this.setState({
      showDelete: false,
      item: {}
    });
  };
  onDeleteSuccess = () => {
    this.setState({
      showDelete: false,
      item: {}
    });
    this.props.receiveItemList();
  };
  onEdit = (e) => {
    const targetId = e.target.id;
    const itemUuid = targetId.split(':')[1];
    const { items = {} } = this.props;
    const itemsData = items.items || [];
    const item = itemsData.find((record) => itemUuid === record.itemUuid);
    if (item) {
      this.setState({
        showEdit: true,
        item
      });
    }
  };
  onEditClose = () => {
    this.setState({
      showEdit: false,
      item: {}
    });
  };
  onEditSuccess = () => {
    this.setState({
      showEdit: false,
      item: {}
    });
    this.props.receiveItemList();
  };
  render() {
    const { items = {} } = this.props;
    const { showDelete, item, showEdit } = this.state;
    const { loading = true } = items;
    const itemsRecord = items.items || [];
    if (loading) {
      return <Loader />;
    }
    const rows = [];
    itemsRecord.forEach((item) => {
      const toggleButton = (
        <Dropdown>
          <Dropdown.Toggle split variant="outline-success" id="dropdown-split-basic" />
          <Dropdown.Menu>
            <Dropdown.Item id={`EDIT:${item.itemUuid}`} onClick={this.onEdit}>
              Edit
            </Dropdown.Item>
            <Dropdown.Item id={`DELETE:${item.itemUuid}`} onClick={this.onDelete}>
              Delete
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      );
      const row = (
        <tr key={`ROW:${item.itemUuid}`}>
          <td>{item.name}</td>
          <td>{item.prices.toString()}</td>
          <td>{toggleButton}</td>
        </tr>
      );
      rows.push(row);
    });

    return (
      <>
        <ItemDelete
          show={showDelete}
          itemData={item}
          onHandleClose={this.onDeleteClose}
          onHandleSuccess={this.onDeleteSuccess}
        />
        <ItemEdit
          show={showEdit}
          itemData={item}
          onHandleClose={this.onEditClose}
          onHandleSuccess={this.onEditSuccess}
        />
        <Table responsive striped bordered hover>
          <thead>
            <tr>
              <th>Name</th>
              <th>Prices</th>
              <th></th>
            </tr>
          </thead>
          <tbody>{rows}</tbody>
        </Table>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  items: state.items
});

const mapDispatchToProps = (dispatch) => ({
  receiveItemList: () => dispatch(receiveItemList()),
  resetState: () => dispatch(resetItemsState())
});
export default connect(mapStateToProps, mapDispatchToProps)(ItemTable);
