import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal } from 'react-bootstrap';
import { removeCustomerAction, resetCustomerState } from '../../actions';
import { connect } from 'react-redux';

import Loader from '../Loader/Loader';

class CustomerDelete extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidUpdate(prevProps) {
    const { customer = {} } = this.props;
    const prevCustomer = prevProps.customer || {};
    if (prevCustomer.loading !== customer.loading) {
      if (customer.loading === false && customer.success) {
        this.handleSuccess();
      } else if (customer.error) {
        console.error(customer.error);
      }
    }
  }

  componentWillUnmount() {
    this.props.resetState();
  }

  handleClose = () => {
    this.props.onHandleClose();
  };
  handleSuccess = () => {
    this.props.onSuccess();
  };
  handleSubmit = (event) => {
    const { customerData } = this.props;
    event.preventDefault();
    this.props.removeCustomer(customerData.customerUuid);
  };

  getForm = () => {
    const { customerData = {}, customer = {} } = this.props;
    const { loading = false } = customer;
    if (loading) {
      return <Loader />;
    }
    return (
      <div>
        Are you sure you want to delete Customer <b>{customerData.name}</b> from the system
      </div>
    );
  };

  render() {
    const { show } = this.props;
    const formComponent = this.getForm();
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Delete Customer</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ textAlign: 'center' }}>{formComponent}</div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="danger" onClick={this.handleSubmit}>
            Submit
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
CustomerDelete.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onSuccess: PropTypes.func,
  customerData: PropTypes.object
};
const mapStateToProps = (state) => ({
  customer: state.customer
});

const mapDispatchToProps = (dispatch) => ({
  removeCustomer: (id) => dispatch(removeCustomerAction(id)),
  resetState: () => dispatch(resetCustomerState())
});
export default connect(mapStateToProps, mapDispatchToProps)(CustomerDelete);
