import axios from 'axios';

export const FETCH_HEADERS = {
  'Content-Type': 'application/json'
};

const AxiosService = {
  defaultHeaders: FETCH_HEADERS,
  deleteRequest: ({ headers = FETCH_HEADERS, url, params = {}, data = {} }) => {
    const queryParams = Object.assign({}, params);
    return axios
      .delete(url, {
        headers,
        data,
        params: queryParams
      })
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  },

  getRequest: ({ headers = FETCH_HEADERS, url, params = {}, options = {} }) => {
    const queryParams = Object.assign({}, params);
    const { source } = options;
    return axios
      .get(url, {
        headers,
        params: queryParams,
        cancelToken: source && source.token
      })
      .then((response) => Promise.resolve(response))
      .catch((error) => {
        if (axios.isCancel(error)) {
          console.log(`GET Request ${url} has been canceled`, error.message);
        }
        return Promise.reject(error);
      });
  },

  postRequest: ({ headers = FETCH_HEADERS, url, data, options = {} }) => {
    const { source } = options;
    return axios
      .post(url, data, {
        headers,
        cancelToken: source && source.token
      })
      .then((response) => Promise.resolve(response))
      .catch((error) => {
        if (axios.isCancel(error)) {
          console.log('POST request ${url} has been canceled', error.message);
        }
        return Promise.reject(error);
      });
  },

  putRequest: ({ headers = FETCH_HEADERS, url, data, options = {} }) => {
    const { source } = options;
    return axios
      .put(url, data, {
        headers,
        cancelToken: source && source.token
      })
      .then((response) => Promise.resolve(response))
      .catch((error) => {
        if (axios.isCancel(error)) {
          console.log('PUT request ${url} has been canceled', error.message);
        }
        return Promise.reject(error);
      });
  }
};
export default AxiosService;
