import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal } from 'react-bootstrap';
import { removeItemAction, resetItemState } from '../../actions';
import { connect } from 'react-redux';

import Loader from '../Loader/Loader';

class ItemDelete extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidUpdate(prevProps) {
    const { item = {} } = this.props;
    const prevItem = prevProps.item || {};
    if (prevItem.loading !== item.loading) {
      if (item.loading === false && item.success) {
        this.handleSuccess();
      } else if (item.error) {
        console.error(item.error);
      }
    }
  }

  componentWillUnmount() {
    this.props.resetState();
  }

  handleClose = () => {
    this.props.onHandleClose();
  };
  handleSuccess = () => {
    this.props.onHandleSuccess();
  };
  handleSubmit = (event) => {
    const { itemData } = this.props;
    event.preventDefault();
    this.props.removeItem(itemData.itemUuid);
  };

  getForm = () => {
    const { itemData = {}, item = {} } = this.props;
    const { loading = false } = item;
    if (loading) {
      return <Loader />;
    }
    return (
      <div>
        Are you sure you want to delete Item <b>{itemData.name}</b> from the system
      </div>
    );
  };

  render() {
    const { show } = this.props;
    const formComponent = this.getForm();
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Delete Item</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ textAlign: 'center' }}>{formComponent}</div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="danger" onClick={this.handleSubmit}>
            Submit
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
ItemDelete.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func,
  itemData: PropTypes.object
};
const mapStateToProps = (state) => ({
  item: state.item
});

const mapDispatchToProps = (dispatch) => ({
  removeItem: (id) => dispatch(removeItemAction(id)),
  resetState: () => dispatch(resetItemState())
});
export default connect(mapStateToProps, mapDispatchToProps)(ItemDelete);
