export const DELETE_ORDER = 'DELETE_ORDER';
export const DELETE_ORDER_SUCCESS = 'DELETE_ORDER_SUCCESS';
export const DELETE_ORDER_FAILURE = 'DELETE_ORDER_FAILURE';
export const CREATE_ORDER = 'CREATE_ORDER';
export const CREATE_ORDER_SUCCESS = 'CREATE_ORDER_SUCCESS';
export const CREATE_ORDER_FAILURE = 'CREATE_ORDER_FAILURE';
export const EDIT_ORDER = 'EDIT_ORDER';
export const EDIT_ORDER_SUCCESS = 'EDIT_ORDER_SUCCESS';
export const EDIT_ORDER_FAILURE = 'EDIT_ORDER_FAILURE';
export const DEFAULT_ORDER_STATE = 'DEFAULT_ORDER_STATE';
export function removeOrderAction(payload = {}) {
  return {
    type: DELETE_ORDER,
    payload
  };
}

export function removeOrderSuccess(payload = {}) {
  return {
    type: DELETE_ORDER_SUCCESS,
    payload
  };
}

export function removeOrderFailure(error) {
  return {
    type: DELETE_ORDER_FAILURE,
    error
  };
}
export function createOrderAction(payload = {}) {
  return {
    type: CREATE_ORDER,
    payload
  };
}

export function createOrderSuccess(payload = {}) {
  return {
    type: CREATE_ORDER_SUCCESS,
    payload
  };
}

export function createOrderFailure(error) {
  return {
    type: CREATE_ORDER_FAILURE,
    error
  };
}
export function editOrderAction(id, payload = {}) {
  return {
    type: EDIT_ORDER,
    id,
    payload
  };
}

export function editOrderSuccess(payload = {}) {
  return {
    type: EDIT_ORDER_SUCCESS,
    payload
  };
}

export function editOrderFailure(error) {
  return {
    type: EDIT_ORDER_FAILURE,
    error
  };
}
export function resetOrderState() {
  return {
    type: DEFAULT_ORDER_STATE
  };
}
