export const FETCH_PAYMENT_LIST = 'FETCH_PAYMENT_LIST';
export const FETCH_PAYMENT_LIST_SUCCESS = 'FETCH_PAYMENT_LIST_SUCCESS';
export const FETCH_PAYMENT_LIST_FAILURE = 'FETCH_PAYMENT_LIST_FAILURE';
export const DEFAULT_PAYMENTS_STATE = 'DEFAULT_PAYMENTS_STATE';
export function receivePaymentList() {
  return {
    type: FETCH_PAYMENT_LIST
  };
}

export function receivePaymentListSuccess(payload = {}) {
  return {
    type: FETCH_PAYMENT_LIST_SUCCESS,
    payload
  };
}

export function receivePaymentListFailure(error) {
  return {
    type: FETCH_PAYMENT_LIST_FAILURE,
    error
  };
}

export function resetPaymentsState() {
  return {
    type: DEFAULT_PAYMENTS_STATE
  };
}
