import React from 'react';
import { Button, Card, Container } from 'react-bootstrap';
import { connect } from 'react-redux';

import ItemTable from './ItemTable';
import ItemCreate from './ItemCreate';
import './Item.css';

import { receiveItemList } from '../../actions';

class Item extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showCreate: false
    };
  }
  onCreateShow = () => {
    this.setState({
      showCreate: true
    });
  };
  onCreateClose = () => {
    this.setState({
      showCreate: false
    });
  };
  onCreateSuccess = () => {
    this.props.receiveItemList();
    this.setState({
      showCreate: false
    });
  };
  render() {
    const { showCreate } = this.state;
    return (
      <Container fluid md="auto" className="item-main">
        <ItemCreate
          show={showCreate}
          onHandleClose={this.onCreateClose}
          onHandleSuccess={this.onCreateSuccess}
        />
        <Card style={{ backgroundColor: '#FFFFE0' }}>
          <Card.Header>
            Item List
            <Button size="sm" variant="primary" className="float-end" onClick={this.onCreateShow}>
              Add
            </Button>
          </Card.Header>
          <Card.Body>
            <ItemTable />
          </Card.Body>
        </Card>
      </Container>
    );
  }
}

const mapDispatchToProps = (dispatch) => ({
  receiveItemList: () => dispatch(receiveItemList())
});

export default connect(null, mapDispatchToProps)(Item);
