import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal, Form, Col, InputGroup, Row } from 'react-bootstrap';
import { editItemAction, resetItemState } from '../../actions';
import { connect } from 'react-redux';
import Loader from '../Loader/Loader';

class ItemEdit extends React.Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.nameRef = React.createRef();
    this.priceRef = React.createRef();
    this.state = {
      validated: false
    };
  }
  componentDidUpdate(prevProps) {
    const { item = {} } = this.props;
    const prevItem = prevProps.item || {};
    if (item.loading !== prevItem.loading) {
      if (!item.loading && item.success) {
        this.handleClose();
      } else if (item.error) {
        console.error(item.error);
      }
    }
  }
  componentWillUnmount() {
    this.props.resetState();
  }
  handleClose = () => {
    this.props.onHandleClose();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSuccess = () => {
    this.props.onHandleClose();
    this.formRef.current.reset();
    this.setState({ validated: false });
    this.props.onHandleSuccess();
  };
  handleSubmit = (event) => {
    const { itemUuid } = this.props.itemData;
    const form = this.formRef.current;
    event.preventDefault();
    if (form.checkValidity()) {
      const name = this.nameRef.current.value;
      let price = this.priceRef.current.value;
      price = price.replaceAll(' ', '');
      const prices = price
        .split(',')
        .filter((element) => element !== '')
        .map(Number);
      this.props.editItem(itemUuid, { name, prices });
      this.setState({
        validated: false
      });
    } else {
      event.stopPropagation();
      this.setState({
        validated: true
      });
    }
  };

  getForm = (validated) => {
    const { item = {}, itemData = {} } = this.props;
    const { name = '', prices = [] } = itemData;
    const { loading = false } = item;
    if (loading) {
      return <Loader />;
    }
    return (
      <Form
        noValidate
        validated={validated}
        ref={this.formRef}
        onSubmit={(e) => this.handleSubmit(e)}>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalType">
          <Form.Label column sm={2}>
            Name
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={255}
                autoComplete="off"
                type="text"
                placeholder="Name"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.nameRef}
                defaultValue={name}
              />
              <Form.Control.Feedback type="invalid">Enter a Name.</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Label column sm={2}>
            Price
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={255}
                autoComplete="off"
                type="text"
                placeholder="Price comma seperated (eg. 100,200)"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.priceRef}
                defaultValue={prices.toString()}
              />
              <Form.Control.Feedback type="invalid">
                Enter price comma seperated
              </Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
      </Form>
    );
  };

  render() {
    const { show } = this.props;
    const { validated } = this.state;
    const formComponent = this.getForm(validated);
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Edit Item</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ textAlign: 'center' }}>{formComponent}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={this.handleSubmit}>
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
ItemEdit.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func,
  itemData: PropTypes.object
};
const mapStateToProps = (state) => ({
  item: state.item
});

const mapDispatchToProps = (dispatch) => ({
  editItem: (id, payload) => dispatch(editItemAction(id, payload)),
  resetState: () => dispatch(resetItemState())
});
export default connect(mapStateToProps, mapDispatchToProps)(ItemEdit);
