export const DELETE_CUSTOMER = 'DELETE_CUSTOMER';
export const DELETE_CUSTOMER_SUCCESS = 'DELETE_CUSTOMER_SUCCESS';
export const DELETE_CUSTOMER_FAILURE = 'DELETE_CUSTOMER_FAILURE';
export const CREATE_CUSTOMER = 'CREATE_CUSTOMER';
export const CREATE_CUSTOMER_SUCCESS = 'CREATE_CUSTOMER_SUCCESS';
export const CREATE_CUSTOMER_FAILURE = 'CREATE_CUSTOMER_FAILURE';
export const EDIT_CUSTOMER = 'EDIT_CUSTOMER';
export const EDIT_CUSTOMER_SUCCESS = 'EDIT_CUSTOMER_SUCCESS';
export const EDIT_CUSTOMER_FAILURE = 'EDIT_CUSTOMER_FAILURE';
export const DEFAULT_CUSTOMER_STATE = 'DEFAULT_CUSTOMER_STATE';
export function removeCustomerAction(payload = {}) {
  return {
    type: DELETE_CUSTOMER,
    payload
  };
}

export function removeCustomerSuccess(payload = {}) {
  return {
    type: DELETE_CUSTOMER_SUCCESS,
    payload
  };
}

export function removeCustomerFailure(error) {
  return {
    type: DELETE_CUSTOMER_FAILURE,
    error
  };
}
export function createCustomerAction(payload = {}) {
  return {
    type: CREATE_CUSTOMER,
    payload
  };
}

export function createCustomerSuccess(payload = {}) {
  return {
    type: CREATE_CUSTOMER_SUCCESS,
    payload
  };
}

export function createCustomerFailure(error) {
  return {
    type: DELETE_CUSTOMER_FAILURE,
    error
  };
}
export function editCustomerAction(id, payload = {}) {
  return {
    type: EDIT_CUSTOMER,
    id,
    payload
  };
}

export function editCustomerSuccess(payload = {}) {
  return {
    type: EDIT_CUSTOMER_SUCCESS,
    payload
  };
}

export function editCustomerFailure(error) {
  return {
    type: EDIT_CUSTOMER_FAILURE,
    error
  };
}
export function resetCustomerState() {
  return {
    type: DEFAULT_CUSTOMER_STATE
  };
}
