import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal } from 'react-bootstrap';
import { removeOrderAction, resetOrderState } from '../../actions';
import { connect } from 'react-redux';

import Loader from '../Loader/Loader';

class SimpleOrderDelete extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidUpdate(prevProps) {
    const { order = {} } = this.props;
    const prevOrder = prevProps.order || {};
    if (prevOrder.loading !== order.loading) {
      if (order.loading === false && order.success) {
        this.handleSuccess();
      } else if (order.error) {
        console.error(order.error);
      }
    }
  }

  componentWillUnmount() {
    this.props.resetState();
  }

  handleClose = () => {
    this.props.onHandleClose();
  };
  handleSuccess = () => {
    this.props.onHandleSuccess();
  };
  handleSubmit = (event) => {
    const { orderData } = this.props;
    event.preventDefault();
    this.props.removeOrder(orderData.orderUuid);
  };

  getForm = () => {
    const { orderData = {}, order = {} } = this.props;
    const { loading = false } = order;
    if (loading) {
      return <Loader />;
    }
    return (
      <div>
        Are you sure you want to delete Order of Amount <b>{orderData.total}</b> from the system
      </div>
    );
  };

  render() {
    const { show } = this.props;
    const formComponent = this.getForm();
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Delete Order</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ textAlign: 'center' }}>{formComponent}</div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="danger" onClick={this.handleSubmit}>
            Submit
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
SimpleOrderDelete.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func,
  orderData: PropTypes.object
};
const mapStateToProps = (state) => ({
  order: state.order
});

const mapDispatchToProps = (dispatch) => ({
  removeOrder: (id) => dispatch(removeOrderAction(id)),
  resetState: () => dispatch(resetOrderState())
});
export default connect(mapStateToProps, mapDispatchToProps)(SimpleOrderDelete);
