import {
  DELETE_CUSTOMER,
  DELETE_CUSTOMER_SUCCESS,
  DELETE_CUSTOMER_FAILURE,
  DEFAULT_CUSTOMER_STATE,
  CREATE_CUSTOMER,
  CREATE_CUSTOMER_SUCCESS,
  CREATE_CUSTOMER_FAILURE,
  EDIT_CUSTOMER,
  EDIT_CUSTOMER_FAILURE,
  EDIT_CUSTOMER_SUCCESS
} from '../actions';

const defaultState = {};
function customer(state = defaultState, action = {}) {
  const { payload = {}, error } = action;
  switch (action.type) {
    case DELETE_CUSTOMER: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case DELETE_CUSTOMER_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case DELETE_CUSTOMER_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case CREATE_CUSTOMER: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case CREATE_CUSTOMER_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case CREATE_CUSTOMER_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case EDIT_CUSTOMER: {
      return {
        ...state,
        loading: true,
        error: null
      };
    }
    case EDIT_CUSTOMER_SUCCESS: {
      return {
        ...state,
        ...payload,
        success: true,
        loading: false,
        error: null
      };
    }
    case EDIT_CUSTOMER_FAILURE: {
      return {
        ...state,
        success: false,
        loading: false,
        error
      };
    }
    case DEFAULT_CUSTOMER_STATE: {
      return {};
    }
    default:
      return state;
  }
}

export default customer;
