import React from 'react';
import PropTypes from 'prop-types';
import { Button, Modal, Form, Col, InputGroup, Row } from 'react-bootstrap';
import { connect } from 'react-redux';

import { editOrderAction, resetOrderState } from '../../actions';

import Loader from '../Loader/Loader';

class SimpleOrderEdit extends React.Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.amountRef = React.createRef();
    this.billRef = React.createRef();
    this.state = {
      validated: false
    };
  }
  componentWillUnmount() {
    this.props.resetState();
  }
  componentDidUpdate(prevProps) {
    const { order = {} } = this.props;
    const prevOrder = prevProps.order || {};
    if (order.loading !== prevOrder.loading) {
      if (!order.loading && order.success) {
        this.handleSuccess();
      } else if (order.error) {
        console.error(order.error);
      }
    }
  }

  handleClose = () => {
    this.props.onHandleClose();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSuccess = () => {
    this.props.onHandleSuccess();
    if (this.formRef && this.formRef.current) {
      this.formRef.current.reset();
    }
    this.setState({ validated: false });
  };
  handleSubmit = (event) => {
    const form = this.formRef.current;
    event.preventDefault();
    if (form.checkValidity()) {
      const amount = this.amountRef.current.value;
      const remark = this.billRef.current.value || '';
      const { orderData = {} } = this.props;
      const { orderUuid } = orderData;
      this.props.editOrder(orderUuid, {
        remark,
        items: {
          LUMP_SUM: {
            quantity: 1,
            price: Number(amount)
          }
        }
      });
      this.setState({
        validated: false
      });
    } else {
      event.stopPropagation();
      this.setState({
        validated: true
      });
    }
  };

  getForm = (validated) => {
    const { order = {}, orderData = {} } = this.props;
    const { loading = false } = order;
    const { total, remark = '' } = orderData;
    if (loading) {
      return <Loader />;
    }
    return (
      <Form
        noValidate
        validated={validated}
        ref={this.formRef}
        onSubmit={(e) => this.handleSubmit(e)}>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Label column sm={2}>
            Total
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={10}
                autoComplete="off"
                type="number"
                placeholder="Total amount"
                aria-describedby="inputGroupPrepend"
                required
                ref={this.amountRef}
                defaultValue={total}
              />
              <Form.Control.Feedback type="invalid">Enter total amount</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalValue">
          <Form.Label column sm={2}>
            Bill No.
          </Form.Label>
          <Col sm={10}>
            <InputGroup hasValidation>
              <Form.Control
                maxLength={5}
                autoComplete="off"
                type="text"
                placeholder="Bill Number"
                aria-describedby="inputGroupPrepend"
                ref={this.billRef}
                defaultValue={remark}
              />
              <Form.Control.Feedback type="invalid">Enter bill number</Form.Control.Feedback>
            </InputGroup>
          </Col>
        </Form.Group>
      </Form>
    );
  };

  render() {
    const { show } = this.props;
    const { validated } = this.state;
    const formComponent = this.getForm(validated);
    return (
      <Modal show={show} onHide={this.handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Complete Order</Modal.Title>
        </Modal.Header>
        <Modal.Body>{formComponent}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={this.handleSubmit}>
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
SimpleOrderEdit.propTypes = {
  show: PropTypes.bool,
  onHandleClose: PropTypes.func,
  onHandleSuccess: PropTypes.func,
  orderData: PropTypes.object
};
const mapStateToProps = (state) => ({
  order: state.order
});
const mapDispatchToProps = (dispatch) => ({
  editOrder: (id, payload) => dispatch(editOrderAction(id, payload)),
  resetState: () => dispatch(resetOrderState())
});
export default connect(mapStateToProps, mapDispatchToProps)(SimpleOrderEdit);
