export const FETCH_CUSTOMER_LIST = 'FETCH_CUSTOMER_LIST';
export const FETCH_CUSTOMER_LIST_SUCCESS = 'FETCH_CUSTOMER_LIST_SUCCESS';
export const FETCH_CUSTOMER_LIST_FAILURE = 'FETCH_CUSTOMER_LIST_FAILURE';
export const DEFAULT_CUSTOMERS_STATE = 'DEFAULT_CUSTOMERS_STATE';
export function receiveCustomerList() {
  return {
    type: FETCH_CUSTOMER_LIST
  };
}

export function receiveCustomerListSuccess(payload = {}) {
  return {
    type: FETCH_CUSTOMER_LIST_SUCCESS,
    payload
  };
}

export function receiveCustomerListFailure(error) {
  return {
    type: FETCH_CUSTOMER_LIST_FAILURE,
    error
  };
}

export function resetCustomersState() {
  return {
    type: DEFAULT_CUSTOMERS_STATE
  };
}
